.blog{
    .section__title{
        margin-bottom: 80px;
    }
    &__post{
        border-radius: 6px;
        overflow: hidden;
        background: $white;
        margin-bottom: 50px;
        box-shadow: $main-shadow;
        .post-content{
            padding: 30px;
            .post-title{
                margin-bottom: 20px;
                a{
                    color: $text-color-dark;
                    font-size: 27px;
                    text-decoration: none;
                    font-weight: 700;
                }
            }
            .post-excerpt{
                margin-bottom: 20px;
            }
            a{
                font-family: $primary-font;
                color: $text-color-dark;
                font-size: 18px;
                font-weight: 500;
            }
        }
        &.featured{
            display: flex;
            align-items: center;
            padding: 40px;
            .post-thumb{
                flex-basis: 40%;
            }
            .post-content{
                flex-basis: 60%;
                padding: 0 0 0 40px;
            }
        }
    }
    &__date{
        text-align: center;
        position: relative;
        padding-top: 20px;
        margin-right: 20px;
        &::before{
            position: absolute;
            content: '';
            left: 0;
            top: 3px;
            height: 2px;
            width: 100%;
            background: $primary-color;
        }
    }
    &__content{
        h3{
            margin-bottom: 20px;
        }
        p{
            margin-bottom: 30px;
        }
        img{
            margin-bottom: 100px;
            box-shadow: $main-shadow;
            &.mobile{
                width: 500px;
                height: auto;
            }
        }
    }
}