body {
    background: $body-color;
}

.section {
    padding: 100px 0;

    &__title-center {
        margin-bottom: 80px;
        position: relative;

        &::before {
            position: absolute;
            content: '';
            width: 100px;
            height: 10px;
            bottom: -17px;
            left: 50%;
            transform: translateX(-50%);
            background-color: $secondary-color;
            border-radius: 10px;
        }
    }
}

a {
    color: $primary-color;
}

.main-shadow {
    box-shadow: $main-shadow;
}

.page{
    &__header{
        padding: 200px 0 100px;
        background-image: $primary-gradient;
    }
    &__title{
        color: $white;
        margin-bottom: 30px;
    }
    &__breadcrumb{
        li{
            font-family: $primary-font;
            color: $white;
            font-size: 18px;
            a{
                color: $white;
                font-weight: 700;
                text-decoration: none;
                transition: .3s ease;
                i{
                    font-size: 14px;
                    display: inline-block;
                }
            }
        }
    }
}

a{
    color: $primary-color;
    &:hover{
        color: $primary-color;
    }
}

.text-link{
    border-bottom: 1px dotted $primary-color;
    &:hover{
        border-bottom: 1px solid $primary-color;
        text-decoration: none;
    }
}

.bg-primary-gradient{
    background-image: $primary-gradient;
}

// Bootstrap Form Control
.form-control{
    height: 55px;
    font-size: 14px;
    padding: 10px 20px;
    font-family: $primary-font;
    font-weight: 600;
    &:focus{
        outline: 0;
        box-shadow: none;
        border-color: $primary-color;
    }
}

textarea.form-control{
    height: auto;
}

.bg-primary{
    background: $primary-color !important;
}

.bg-secondary{
    background: $secondary-color !important;
}