.trial{
    &__block{
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
        padding: 50px;
        position: relative;
        img{
            width: 350px;
            margin-bottom: 50px;
        }
        p{
            margin-bottom: 50px;
        }
        .scroll__down{
            position: absolute;
            left: 50%; transform: translateX(-50%);
            bottom: 40px;
            z-index: 2;
            text-align: center;
            &:hover{
                text-decoration: none;
            }
            i{
                font-size: 30px;
                color: $primary-color;
                animation: updown .8s ease-in infinite alternate-reverse;
                display: block;
            }
        }
    }
    &__account{
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
        padding: 50px;
    }
    &__details{
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
        padding: 50px;
        .user{
            img{
                border-radius: 100%;
                box-shadow: $main-shadow;
                margin-bottom: 30px;
            }
            h6{
                margin-bottom: 20px;
            }
            padding-bottom: 30px;
            margin-bottom: 30px;
            border-bottom: 1px solid $border-color;
        }
        .posts{
            .post-thumb{
                img{
                    border-radius: 6px;
                    box-shadow: $main-shadow;
                }
                position: relative;
                .like-number{
                    position: absolute;
                    top: 50%; left: 50%;
                    transform: translate(-50%, -50%);
                    i,span{
                        display: inline-block;
                        color: $white;
                        font-size: 20px;
                        line-height: 20px;
                    }
                    i{
                        margin-right: 10px;
                    }
                    span{
                        font-family: $primary-font;
                        font-weight: 600;
                    }
                }
            }
            padding-bottom: 30px;
            margin-bottom: 30px;
            border-bottom: 1px solid $border-color;
        }
        .order{
            .order-item{
                padding: 20px;
                border-radius: 6px; 
                box-shadow: $main-shadow;
                display: flex;
                align-items: center;
                position: relative;
                img{
                    width: 70px; height: 70px;
                    border-radius: 4px;
                    margin-right: 20px;
                }
                i{
                    position: absolute;
                    top: 20px; right: 20px;
                    cursor: pointer;
                }
                margin-bottom: 30px;
            }
        }
    }
    &__submit{
        background: $white;
        border-radius: 6px;
        box-shadow: $main-shadow;
        padding: 50px;
    }
}