.hero__area{
    padding: 200px 0 150px;
    background-image: url('../images/backgrounds/hero-bg.svg');
    background-size: cover;
    background-position: bottom center;
    position: relative;
    &-content{
        h2, p{
            color: $white;
        }
        h2{
            margin-bottom: 20px;
        }
        p{
            width: 70%;
            margin-bottom: 30px;
        }
    }
    &-images{
        position: relative;
        height: 580px;
        .floating-illu{
            position: absolute;
        }
        .large{
            bottom: 0;
            left: 0;
            svg{
                width: 320px;
                height: auto;
            }
            animation: updown_illustration 4s ease-in infinite alternate-reverse;
        }
        .medium{
            left: 50%;
            transform: translateX(-50%);
            top: 0;
            animation: updown_illustration_middle 5s ease-in infinite alternate-reverse;
            animation-delay: 1s;
            svg{
                width: 350px;
                height: auto;
            }
            
        }
        .small{
            right: 0; bottom: -30px;
            animation: updown_illustration 4s ease-in infinite alternate-reverse;
            animation-delay: 1.5s;
            svg{
                width: 320px;
            }
        }
    }
    .scroll__down{
        position: absolute;
        left: 50%; transform: translateX(-50%);
        bottom: 40px;
        z-index: 2;
        text-align: center;
        &:hover{
            text-decoration: none;
        }
        i{
            font-size: 30px;
            color: $white;
            animation: updown .8s ease-in infinite alternate-reverse;
        }
        span{
            color: $white;
            font-size: 10px;
            text-transform: uppercase;
            font-family: $primary-font;
            letter-spacing: 3px;
        }
        span, i{
            display: block;
        }
    }
}

@keyframes updown{
    0% {
        transform: translateY(0);
    }
    100%{
        transform: translateY(10px);
    }
}

@keyframes updown_illustration{
    0% {
        transform: translateY(0);
    }
    100%{
        transform: translateY(15px);
    }
}

@keyframes updown_illustration_middle{
    0% {
        transform: translate(-50%, 0);
    }
    100%{
        transform: translate(-50%, 15px);
    }
}