// 1.2. Button Styles
.btn {
    font-size: 16px;
    border-radius: 4px;
    padding: 18px 55px;
    border: none;
    letter-spacing: 1px;
    font-family: $primary-font;
    font-weight: 500;
    transition: .3s ease;

    &:hover {
        box-shadow: $main-shadow;
    }
}

.btn-primary {
    background: $primary-color;
    color: $white;

    &:hover {
        background: $primary-color;
        color: $white;
    }
}

.btn-secondary {
    background: $secondary-color;
    color: $white;

    &:hover {
        background: $secondary-color;
        color: $white;
    }
}

.btn-sm {
    padding: 15px 30px;
}

.btn-lg {
    padding: 15px 80px;
}

.btn-white {
    background: $white;
    color: $primary-color;
}