.feature{
    &__item{
        margin-bottom: 50px;
        i{
            font-size: 40px;
            color: $primary-color;
            display: inline-block;
            margin-bottom: 15px;
        }
        h3{
            margin-bottom: 10px;
            font-size: 27px;
            color: $text-color-dark;
        }
        p{
            line-height: 28px;
        }
        background: $white;
        padding: 35px 45px 30px;
        box-shadow: 0px 15px 30px 0px rgba(0, 0, 0, 0.06);
        border-radius: 6px;
        &.violet{
            border-bottom: 3px solid #c09df6;
        }
        &.red{
            border-bottom: 3px solid #f6ae9d;
        }
        &.green{
            border-bottom: 3px solid #1ecfd5;
        }
        &.orange{
            border-bottom: 3px solid #ffc64c;
        }
    }
    .cta{
        margin-top: 100px;
        h3{
            margin-bottom: 20px;
        }
        p{
            margin-bottom: 20px;
        }
    }
}