.service {
    padding: 100px 0;

    &__item {
        padding: 100px 0;
    }

    &__content {
        h3 {
            margin-bottom: 30px;
        }

        p {
            margin-bottom: 30px;
        }
    }

    &__image {
        position: relative;
        height: 450px;

        .image {
            position: absolute;

            img {
                box-shadow: $main-shadow;
            }
        }

        &.followers {
            .right {
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }

            .left-top {
                bottom: 250px;
                left: 0;

                img {
                    border-radius: 100px;
                    max-width: 175px;
                }
            }

            .left-middle {
                bottom: 185px;
                left: 185px;

                img {
                    border-radius: 100px;
                    max-width: 175px;
                }
            }

            .left-bottom {
                bottom: 100px;
                left: 20px;

                img {
                    border-radius: 100px;
                    max-width: 175px;
                }
            }
        }

        &.likes {
            .left {
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }

            .right {
                right: 0;
                bottom: 0;

                img {
                    max-width: 350px;
                }
            }
        }
    }

    &__title-secondary {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            width: 80px;
            height: 10px;
            background-color: $secondary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -15px;
            left: 0;
        }
    }

    &__title-primary {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            width: 80px;
            height: 10px;
            background-color: $primary-color;
            z-index: -1;
            border-radius: 10px;
            bottom: -15px;
            right: 0;
        }
    }
}

/* ==================================================================================
somrat starts from here
===================================================================================== */

.features__item {
    margin-bottom: 100px;
}

.features__image {
    height: 200px;
    width: 200px;
    border-radius: 50%;
    position: relative;
    margin: auto;
    margin-bottom: 20px;

    &.blue {
        background: #EEF1FF;
    }

    &.transparent {
        width: 230px;
        background: none;
    }

    &.pink {
        background: #FBEEFF;
    }

    &.violet {
        background: #F6EEFF;
    }
}

.icon {
    position: absolute;
    width: auto;
    height: 60px;
    border-radius: 100px;
    box-shadow: 0 7px 14px 1px rgba(0, 0, 0, .15);

    &.squre {
        border-radius: 8px;
    }
}

.icon-post {
    border-radius: 8px;
    box-shadow: 0 7px 14px 1px rgba(0, 0, 0, .15);
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
}

.icon-left {
    left: 0;
    top: 0px;
}

.icon-middle {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.icon-right {
    right: 0;
    bottom: 0;
}

.icon-middle-right {
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    &-2 {
        right: -30px;
        top: 50%;
        transform: translateY(-50%);
    }
}

.icon-left-bottom {
    left: 0;
    bottom: 0;
}

.icon-semitop-left {
    top: 30px;
    left: -5px;
}

.icon-semibottom-right {
    bottom: 30px;
    right: -5px;
}

.delay-dot {
    left: -20px;
    bottom: -10px;
}

.support-dot {
    left: -20px;
    top: 30px;
}

.support-dot-2 {
    right: -20px;
    bottom: -10px;
}

.instant-dot {
    bottom: 0px;
    right: -20px;
}

.testimonial {
    &__slider {
        border-radius: 10px;
        background: $white;
        box-shadow: $main-shadow;
        padding: 25px 40px;
        margin-bottom: 30px;
        .prevArrow,
        .nextArrow {
            color: $white;
            font-size: 40px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            border: 0;
            background: transparent;
            outline: 0;
            cursor: pointer;
        }

        .prevArrow {
            left: -50px;
        }

        .nextArrow {
            right: -50px;
        }
    }

    &__slides {
        outline: 0;

        h6 {
            margin-bottom: 10px;
        }

        p {
            margin-bottom: 10px;
        }

        ul {
            li {
                i {
                    color: $primary-color;
                    font-size: 16px;
                }
            }
        }

        span {
            color: $text-color-light;
        }
    }
}

.rating-star {
    li {
        margin: 0 10px;

        i {
            color: $white;
            font-size: 40px;
        }
    }
}

.buy__steps {
    background: $white;
    text-align: center;
    padding: 40px 10px;
    box-shadow: $main-shadow;
    border-radius: 10px;
    margin-bottom: 50px;
}

.expect__list {
    li {
        margin: 0 25px;
    }

    img {
        height: 30px;
        width: auto;
    }
}

.our__service {
    margin-bottom: 50px;

    h4 {
        margin-bottom: 20px;
    }
}

.pricing__item {
    background: $white;
    text-align: center;
    padding: 100px 50px;
    box-shadow: $main-shadow;
    margin-bottom: 50px;

    h4 {
        margin-bottom: 40px;

        span {
            font-weight: 300;
        }
    }

    h2 {
        margin-bottom: 50px;

        span {
            font-size: 25px;
            font-weight: 300;
            vertical-align: text-top;
        }
    }

    h6 {
        margin-top: -31px;
    }

    ul {
        margin-bottom: 50px;

        li {
            list-style-type: none;
            margin: 10px 0;
        }
    }
}