.follower{
  background-image: $secondary-gradient;
  padding: 40px;
  border-radius: 8px;
  margin-bottom: 20px;
  &__counter{
    background: $white;
    border-radius: 8px;
    padding: 40px;
    margin-bottom: 40px;
    img{
      border-radius: 50%;
      margin-bottom: 30px;
    }
    h6{
      margin-bottom: 30px;
    }
    h3{
      margin-bottom: 5px;
    }
    p{
      margin-bottom: 30px;
    }
    a{
      margin-bottom: 30px;
      display: inline-block;
    }
  }
}

.error-msg{
  padding: 15px;
  color: $white;
  background-image: $primary-gradient;
  border-radius: 8px;
}