.faq{
    &__accordion{
        .card{
            background: $white;
            box-shadow: 0px 6px 20px 1px rgba(0, 0, 0, 0.05);;
            margin-bottom: 30px;
            border: none;
            .card-header{
                padding: 0;
                background: $white;
                border: none;
                h5{
                    padding: 30px 30px 30px 60px;
                    font-size: 24px;
                    color: $text-color-dark;
                    cursor: pointer;
                    position: relative;
                    &::before{
                        position: absolute;
                        content: "\e64b";
                        font-family: $icon-font;
                        left: 25px;
                        font-size: 20px;
                        top: 50%; transform: translateY(-50%);
                        color: $primary-color;
                        transition: .3s ease;
                    }
                    &.collapsed{
                        &::before{
                            transform: translateY(-50%) rotate(-90deg);
                        }
                    }
                }
            }
            .card-body{
                padding: 0 30px 30px 60px;
                color: $text-color;
                font-size: 16px;
                line-height: 28px;
            }
        }
    }
}